@media (max-width: 1200px){
    html{
        font-size: 55%;
    }
}

@media (max-width: 991px){
    header{
       padding: 2rem;
    }

    section{
        padding: 2rem;
    }
}

@media (max-width: 768px){
    #menu-bar{
        display: initial;
    }

    header{
        .navbar{
              position: absolute;
              top: 100%;
              right: 0;
              left: 0;
              background-color: #333;
              border-top: .1rem solid rgba(255, 255, 255, 0.2);
              padding: 1rem 2rem;
              clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);

              a{
                  display: block;
                  border-radius: .5rem;
                  padding: 1.5rem;
                  margin: 1.5rem 0;
                  background-color: #222;
              }
        }

        .active-three{
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        }
    }
}

@media (max-width: 450px){
    html{
        font-size: 50%;
    }

    .heading{
        span{
            font-size: 2.5rem;
        }
    }

    .contact{
        .row{
            form{
                .inputBox{
                    input{
                        width: 100%;
                    }
                }
            }
        }
    }
}